<template>
  <el-container>
    <el-header class="cheader">
      <el-row type="flex">
        <el-col :span="6">
          <h2>账号管理</h2>
        </el-col>
        <el-col :span="12"></el-col>
        <el-col :span="6">
          <el-button type="primary" icon="el-icon-plus" @click="newAccount" size="small">新建账号</el-button>
        </el-col>
      </el-row>
    </el-header>
    <el-main class="cmain">
      <el-table :data="accountsTable" :key="Math.random()">
        <el-table-column prop="account" label="名称" width="120"></el-table-column>
        <el-table-column prop="balance" label="余额"></el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<style>
</style>

<script>
export default {
  data() {
    return {
      accountsTable: [
        {
          account: "defalut",
          balance: 1
        }
      ]
    };
  },
  methods: {
    listAccount2table(listAccounts) {
      let tmpTable = [];
      for (let k in listAccounts) {
        tmpTable.push({
          account: k,
          balance: listAccounts[k]
        });
      }
      return tmpTable;
    },
    newAccount() {
      this.$router.push({ path: "/account/new" });
    }
  },
  mounted() {
    let _this = this;
    this.$axios({
      method: "post",
      data: JSON.stringify({
        id: new Date().getTime(),
        method: "account_list",
        params: null
      })
    }).then(response => {
      if (typeof response.data.error != "undefined") {
        this.$alert("错误: 请稍后重试。 /n" + response.data.error, {
          showClose: false,
          closeOnClickModal: false,
          closeOnPressEscape: false,
          confirmButtonText: "确定",
          callback: (action, instance) => {
            _this.$router.push("/");
          }
        });
        return;
      }
      _this.accountsTable = _this.listAccount2table(response.data.result);
      _this.$store.state.Accounts = _this.accountsTable;
    });
  }
};
</script>